<template>
	<view>
		<!-- <view style="font-size:32rpx;color:#1976D2;font-weight:bold;margin:40rpx 0 30rpx 0;text-align:center;">ËøôÈáåÊòØËØæÊó∂ÁªüËÆ°È°µÈù¢</view> -->
		<!-- ËøîÂõûÊåâÈíÆ -->
		<view class="back-btn-container">
			<button class="back-btn" @click="goBack">
				<text class="back-btn-icon">‚Äπ</text>
			</button>
		</view>

		<view class="main-content">
			<view class="page-title">ËØæÊó∂ÁªüËÆ°ÁÆ°ÁêÜ</view>

			<!-- Êù°‰ª∂ÈÄâÊã©Âå∫Âüü -->
			<view class="form-section">
				<!-- ÊïôÂ∏àÈÄâÊã© -->
				<view class="form-item">
					<input class="search-input" v-model.trim="searchKeyword" placeholder="üîç ÊêúÁ¥¢ÊïôÂ∏àÂßìÂêç" />
					<picker @change="onTeacherChange" :value="teacherIndex" :range="filteredTeacherList" range-key="teacherName">
						<view class="picker-display">
							<text>{{ selectedTeacher ? selectedTeacher.teacherName : 'ËØ∑ÈÄâÊã©ÊïôÂ∏à' }}</text>
							<text class="picker-arrow">‚ñº</text>
						</view>
					</picker>
					<!-- ‰ªÖÁÆ°ÁêÜÂëòÂèØÂàáÊç¢ÊïôÂ∏àÔºåÊôÆÈÄöÊïôÂ∏àÂè™ËÉΩÁúãËá™Â∑± -->
				</view>
				<!-- Êúà‰ªΩÈÄâÊã© -->
				<view class="form-item">
					<picker mode="date" fields="month" :value="selectedMonth" @change="onMonthChange">
						<view class="picker-display">
							<text>ÁªüËÆ°Êúà‰ªΩÔºö{{ selectedMonth }}</text>
							<text class="picker-arrow">‚ñº</text>
						</view>
					</picker>
				</view>
			</view>

			<!-- Êü•ËØ¢ÊåâÈíÆ -->
			<view class="action-buttons">
				<button class="query-btn" @click="fetchAndCalculateStatistics" :loading="isLoading">Êü•ËØ¢ÁªüËÆ°</button>
				<button v-if="isAdmin || isSuperAdmin" class="query-btn all-btn" @click="fetchAllTeachersStatistics" :loading="isLoadingAll">
					<text class="btn-icon">üë•</text> Êü•ËØ¢ÂÖ®ÂëòËØæÊó∂
				</button>
			</view>

			<!-- ÁªìÊûúÂ±ïÁ§∫Âå∫Âüü -->
			<view v-if="statistics" class="results-section">
				<view class="results-title">ÁªüËÆ°ÁªìÊûú</view>
				<view class="results-grid">
					<view class="result-item">
						<view class="result-value one-to-one-value">{{ statistics['‰∏ÄÂØπ‰∏Ä'] || 0 }}</view>
						<view class="result-label one-to-one-label">‰∏ÄÂØπ‰∏Ä (Â∞èÊó∂)</view>
					</view>
					<view class="result-item">
						<view class="result-value group-class-value">{{ statistics['Áè≠ËØæ'] || 0 }}</view>
						<view class="result-label group-class-label">Áè≠ËØæ (Â∞èÊó∂)</view>
					</view>
					<view class="result-item">
						<view class="result-value full-time-value">{{ statistics['ËÑ±‰∫ßÁîü'] || 0 }}</view>
						<view class="result-label full-time-label">ËÑ±‰∫ßÁîü (Â∞èÊó∂)</view>
					</view>
				</view>
			</view>
			
			<view v-if="noData && !isLoading" class="empty-state">
				<text>ÂΩìÊúàÊó†ÊéíËØæËÆ∞ÂΩï</text>
			</view>

			<view v-if="(isAdmin || isSuperAdmin) && allStatistics && allStatistics.length" class="results-section card-bg">
				<view class="results-title">ÂÖ®ÂëòËØæÊó∂ÁªüËÆ°</view>
				<view class="all-table">
					<view class="all-table-header">
						<text class="all-th">ÊïôÂ∏àÂßìÂêç</text>
						<text class="all-th">‰∏ÄÂØπ‰∏Ä(Â∞èÊó∂)</text>
						<text class="all-th">Áè≠ËØæ(Â∞èÊó∂)</text>
						<text class="all-th">ËÑ±‰∫ßÁîü(Â∞èÊó∂)</text>
					</view>
					<view v-for="(row, index) in allStatistics" :key="index" class="all-table-row">
						<text class="all-td teacher-name">{{ row.teacherName }}</text>
						<text class="all-td one-to-one-hours">{{ row['‰∏ÄÂØπ‰∏Ä'] }}</text>
						<text class="all-td group-class-hours">{{ row['Áè≠ËØæ'] }}</text>
						<text class="all-td full-time-hours">{{ row['ËÑ±‰∫ßÁîü'] }}</text>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				allTeacherList: [],
				teacherIndex: 0,
				searchKeyword: '',
				selectedMonth: new Date().toISOString().slice(0, 7), // Ê†ºÂºè YYYY-MM
				isLoading: false,
				statistics: null, // Áî®‰∫éÂ≠òÂÇ®ÁªüËÆ°ÁªìÊûú
				noData: false, // ÊòØÂê¶Êó†Êï∞ÊçÆ
				isLoadingAll: false,
				allStatistics: null,
				isSuperAdmin: false,
				isAdmin: false,
			};
		},
		computed: {
			filteredTeacherList() {
				if (!this.searchKeyword) {
					return this.allTeacherList;
				}
				return this.allTeacherList.filter(teacher =>
					teacher.teacherName && teacher.teacherName.toLowerCase().includes(this.searchKeyword.toLowerCase())
				);
			},
			selectedTeacher() {
				if (this.filteredTeacherList.length > 0) {
					return this.filteredTeacherList[this.teacherIndex];
				}
				return null;
			}
		},
		watch: {
			searchKeyword() {
				this.teacherIndex = 0;
			}
		},
		onLoad() {
			this.fetchTeachers();
		},
		onShow() {
			this.checkAdminRole();
		},
		methods: {
			checkAdminRole() {
				const userInfo = uni.getStorageSync('userInfo');
				this.isSuperAdmin = userInfo && userInfo.permission === 'superadmin';
				const hasAdminPermission = userInfo && userInfo.permission === 'ÁÆ°ÁêÜÂëò';
				const hasAdminRole = userInfo && userInfo.role && (userInfo.role.includes('ÁÆ°ÁêÜÂëò') || userInfo.role === 'ÁÆ°ÁêÜÂëò');
				this.isAdmin = hasAdminPermission || hasAdminRole || this.isSuperAdmin;
				// Â¶ÇÊûú‰∏çÊòØÁÆ°ÁêÜÂëòÔºåËá™Âä®ÈÄâ‰∏≠Ëá™Â∑±
				if (!this.isAdmin && !this.isSuperAdmin) {
					const self = this.allTeacherList.find(t => t._id === userInfo._id);
					if (self) {
						this.selectedTeacher = self;
						this.teacherIndex = this.filteredTeacherList.findIndex(t => t._id === self._id);
					}
				}
			},
			fetchTeachers() {
				uniCloud.callFunction({
					name: 'user-center',
					data: { action: 'get-accounts' },
					success: (res) => {
						const result = res.result;
						if (result.code === 200) {
							this.allTeacherList = result.data.filter(acc => {
								if (!acc.role) return false;
								if (Array.isArray(acc.role)) {
									return acc.role.includes('ÊïôÂ∏à');
								} else {
									return acc.role === 'ÊïôÂ∏à';
								}
							});
							// Â¶ÇÊûú‰∏çÊòØÁÆ°ÁêÜÂëòÔºåËá™Âä®ÈÄâ‰∏≠Ëá™Â∑±
							this.checkAdminRole();
						} else {
							uni.showToast({ title: 'Ëé∑ÂèñÊïôÂ∏àÂàóË°®Â§±Ë¥•', icon: 'none' });
						}
					},
					fail: () => {
						uni.showToast({ title: 'Ëé∑ÂèñÊïôÂ∏àÂàóË°®ËØ∑Ê±ÇÂ§±Ë¥•', icon: 'none' });
					}
				});
			},
			onTeacherChange(e) {
				if (!this.isAdmin && !this.isSuperAdmin) return;
				this.teacherIndex = e.detail.value;
				this.selectedTeacher = this.filteredTeacherList[this.teacherIndex];
			},
			onMonthChange(e) {
				this.selectedMonth = e.detail.value;
			},
			fetchAndCalculateStatistics() {
				if (!this.selectedTeacher || !this.selectedTeacher._id) {
					uni.showToast({ title: 'ËØ∑ÂÖàÈÄâÊã©‰∏Ä‰ΩçÊïôÂ∏à', icon: 'none' });
					return;
				}
				this.isLoading = true;
				this.statistics = null;
				this.noData = false;

				const [year, month] = this.selectedMonth.split('-').map(Number);
				const startDate = `${this.selectedMonth}-01`;
				const endDate = new Date(year, month, 0).toISOString().slice(0, 10);

				uniCloud.callFunction({
					name: 'teacher-hours-statistics',
					data: {
						action: 'get-teacher-hours',
						params: {
							teacher_id: this.selectedTeacher._id,
							startDate: startDate,
							endDate: endDate
						}
					},
					success: (res) => {
						const result = res.result;
						if (result.code === 200 && result.data) {
							this.statistics = result.data.statistics;
							// Ê£ÄÊü•ÊòØÂê¶ÊúâËØæÊó∂Êï∞ÊçÆ
							const hasHours = Object.values(result.data.statistics).some(val => val > 0);
							if (!hasHours) {
								this.noData = true;
							}
						} else {
							this.noData = true;
							this.statistics = { '‰∏ÄÂØπ‰∏Ä': 0, 'Áè≠ËØæ': 0, 'ËÑ±‰∫ßÁîü': 0 };
							uni.showToast({ title: result.message || 'Êü•ËØ¢Â§±Ë¥•', icon: 'none' });
						}
					},
					fail: (err) => {
						console.error('Êü•ËØ¢ËØæÊó∂Â§±Ë¥•:', err);
						uni.showToast({ title: 'Êü•ËØ¢ËØæÊó∂Â§±Ë¥•', icon: 'none' });
						this.noData = true;
						this.statistics = { '‰∏ÄÂØπ‰∏Ä': 0, 'Áè≠ËØæ': 0, 'ËÑ±‰∫ßÁîü': 0 };
					},
					complete: () => {
						this.isLoading = false;
					}
				});
			},
			goBack() {
				uni.navigateBack();
			},
			fetchAllTeachersStatistics() {
				this.isLoadingAll = true;
				this.allStatistics = null;
				this.noData = false;
				const [year, month] = this.selectedMonth.split('-').map(Number);
				const startDate = `${this.selectedMonth}-01`;
				const endDate = new Date(year, month, 0).toISOString().slice(0, 10);

				uniCloud.callFunction({
					name: 'teacher-hours-statistics',
					data: {
						action: 'get-all-teachers-hours',
						params: { startDate, endDate }
					},
					success: (res) => {
						const result = res.result;
						console.log('ÂÖ®ÂëòËØæÊó∂ÁªüËÆ°ÁªìÊûú:', result);
						if (result.code === 200 && result.data) {
							// ËΩ¨Êç¢Êï∞ÊçÆÊ†ºÂºè‰ª•ÈÄÇÂ∫îÁé∞ÊúâUI
							const allStats = result.data.map(teacher => {
								return {
									username: teacher.username,
									teacherName: teacher.teacherName || teacher.username,
									'‰∏ÄÂØπ‰∏Ä': teacher.statistics['‰∏ÄÂØπ‰∏Ä'] || 0,
									'Áè≠ËØæ': teacher.statistics['Áè≠ËØæ'] || 0,
									'ËÑ±‰∫ßÁîü': teacher.statistics['ËÑ±‰∫ßÁîü'] || 0
								};
							});
							this.allStatistics = allStats;
							
							// Ê£ÄÊü•ÊòØÂê¶ÊúâÊïôÂ∏àÊúâËØæÊó∂
							const hasActiveTeachers = allStats.some(teacher => 
								teacher['‰∏ÄÂØπ‰∏Ä'] > 0 || teacher['Áè≠ËØæ'] > 0 || teacher['ËÑ±‰∫ßÁîü'] > 0);
							if (!hasActiveTeachers) {
								uni.showToast({ title: 'ÊöÇÊó†ÊïôÂ∏àÊúâËØæÊó∂ËÆ∞ÂΩï', icon: 'none' });
							}
						} else {
							this.allStatistics = [];
							uni.showToast({ title: result.message || 'Êü•ËØ¢Â§±Ë¥•', icon: 'none' });
						}
					},
					fail: (err) => {
						console.error('Êü•ËØ¢ÂÖ®ÂëòËØæÊó∂Â§±Ë¥•:', err);
						uni.showToast({ title: 'Êü•ËØ¢ÂÖ®ÂëòËØæÊó∂Â§±Ë¥•', icon: 'none' });
						this.allStatistics = [];
					},
					complete: () => {
						this.isLoadingAll = false;
					}
				});
			},
			// Êñ∞Â¢ûÔºöËé∑ÂèñËØæÁ®ãÊó∂ÈïøÁöÑÊñπÊ≥ï
			getCourseDuration(courseType) {
				// ÂèØ‰ª•Ê†πÊçÆËØæÁ®ãÁ±ªÂûãËÆæÁΩÆ‰∏çÂêåÁöÑÊó∂Èïø
				switch(courseType) {
					case '‰∏ÄÂØπ‰∏Ä':
						return 2; // ‰∏ÄÂØπ‰∏ÄËØæÁ®ã2Â∞èÊó∂
					case 'Áè≠ËØæ':
						return 2; // Áè≠ËØæ2Â∞èÊó∂
					case 'ËÑ±‰∫ßÁîü':
						return 2; // ËÑ±‰∫ßÁîü2Â∞èÊó∂
					default:
						return 2; // ÈªòËÆ§2Â∞èÊó∂
				}
			}
		}
	};
</script>

<style>
