	position: absolute;
	top: calc(env(safe-area-inset-top) + 90rpx);
	left: 40rpx;
	z-index: 100;
}

.back-btn {
	background: linear-gradient(135deg, #43e97b 0%, #38f9d7 100%); /* ‰øÆÊîπ‰∏∫‰∏é‰øùÂ≠òÊåâÈíÆÁõ∏ÂêåÁöÑÁªøËâ≤Ê∏êÂèò */
	border: none;
	border-radius: 50%;
	width: 80rpx;
	height: 80rpx;
	display: flex;
	align-items: center;
	justify-content: center;
	box-shadow: 0 8rpx 25rpx rgba(67, 233, 123, 0.4);
	transition: all 0.3s ease;
	padding: 0;
	line-height: 1;
}

