	font-size: 26rpx;
	color: #666;
}

/* ‰∏∫‰∏çÂêåËØæÁ®ãÁ±ªÂûãËÆæÁΩÆ‰∏çÂêåÈ¢úËâ≤ */
.one-to-one-value {
	color: #FF6B6B; /* Á∫¢Ëâ≤ */
}
.group-class-value {
	color: #4ECDC4; /* ÈùíËâ≤ */
}
.full-time-value {
	color: #FFD166; /* ÈªÑËâ≤ */
}

.one-to-one-label {
	color: #FF6B6B;
}
