}

/* Êñ∞Â¢ûÂÖ®ÂëòÁªüËÆ°Ë°®Ê†ºÊ†∑Âºè */
.all-table {
	margin-top: 30rpx;
	width: 100%;
}
.all-table-header, .all-table-row {
	display: flex;
	justify-content: space-between;
	align-items: center;
