	color: #4a67d6;
}

/* ÊïôÂ∏àÂßìÂêçÈ¢úËâ≤ */
.teacher-name {
	color: #1976D2; /* ËìùËâ≤ */
	font-weight: bold;
}

/* ‰∏çÂêåËØæÁ®ãÁ±ªÂûãÁöÑÊï∞Â≠óÈ¢úËâ≤ */
.one-to-one-hours {
	color: #FF6B6B; /* Á∫¢Ëâ≤ */
	font-weight: bold;
}

.group-class-hours {
	color: #4ECDC4; /* ÈùíËâ≤ */
	font-weight: bold;
}

.full-time-hours {
	color: #FFD166; /* ÈªÑËâ≤ */
	font-weight: bold;
}
</style>